.Mb-wrap{
    width: 100%;
    /* height: 870px; */
    height: 85vh;
    padding: 0px;
    /* background: blue; */
    position: relative;
    overflow: hidden;
    z-index: -1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Mb-video {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
}

.Mb-img {
    width: 100%;
    height: 100%;
    transform: scale(1);
}
.increaseScale {
    transition: all 2s;
    transform: scale(1.1);
}


.Mb-message {
    color:white;
    z-index: 5;
    font-size: 50px;
    letter-spacing: 10px;
    font-weight: bold;
}


@media screen and (max-width: 556px) {
    .Mb-wrap {
        height: 500px;
    }
}