.Au-wrap {
    // width: 100%;
    // height: 750px;
    // padding-top: 100px;
    // padding-left: 250px;
    // // background: #eee;
    position: relative;
    width: 100%;
    height: 100%;
    max-width: calc(1440px + (min(8rem, 4.17vw)) * 2);
    margin: 0 auto;
    padding: 1px min(8rem,4.17vw);
    margin-bottom: 100px;
}

.Au-textContainer {
    width: 100%;
    height: 600px;
    padding-top: 100px;
    // background: red;
}

.Au-subject {
    font-size: 3rem;
    font-weight: bold;
    margin-bottom: 20px;
}
.Au-content {
    font-size: 20px;
}




@media screen and (max-width:556px) {
    .Au-wrap {
        padding: 100px 20px;
        height: 600px;
    }
    .Au-textContainer {
        width: 446px;
        padding-top: 0px;
    }
    .Au-subject{
        font-size: 30px;
    }
    .Au-content{
        font-size: 13px;
    }
}