.Ht-wrap {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  height: 1000px;
}

.Ht-textCont {
  margin-bottom: 70px;
}

.Ht-text {
  text-align: center;
}

.history {
  font-size: 2rem;
  font-weight: 300;
}

.slogan {
  font-size: 2.8rem;
  font-weight: 600;
  letter-spacing: -0.03em;
  line-height: 2;
}

.text {
  font-size: 1.1rem;
  font-weight: 100;
}
.text p {
  line-height: 10px;
}

.Ht-videoWrap {
  width: 100%;
  max-width: 1156px;
  height: 60%;
  min-height: 200px;
  max-height: 556px;
  overflow: hidden;
  position: relative;
}

.Ht-video {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
}

@media screen and (max-width: 900px) {
  .Ht-wrap {
    height: 800px;
  }
  .text {
    font-size: 0.9rem;
  }
}
@media screen and (max-width: 700px) {
  .Ht-wrap {
    height: 600px;
  }
}/*# sourceMappingURL=History.css.map */